
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'


import Footer from "#/footer";

export default {
data() {
return {
route: '',
tab: 0,
title: '口红机',
header: true,
tabber: true,
bodyPaddingTop: '0px',
bodyPaddingBottom: '0px',

}
},
watch: {
$route: {
handler: function(val, oldVal) {
console.log(val.meta);
const Meta = val.meta
this.title = Meta.title
this.header = Meta.hiddenHeader ? false : true
this.tabber = Meta.hiddenTab ? false : true
this.route = val.name
this.$nextTick(() => {
if (this.$refs.header) {
this.bodyPaddingTop = this.$refs.header.$el.offsetHeight + 'px'
} else {
this.bodyPaddingTop = '0px'
}
if (this.$refs.Footer) {
this.bodyPaddingBottom = this.$refs.Footer.$el.offsetHeight + 'px'
} else {
this.bodyPaddingBottom = '0px'
}
})
},
deep: true,
immediate: true
}
},
computed: {
key() {
return this.$route.fullPath
}
},
components: {
ViewBox,
XHeader,
Footer
},
mounted() {}
}
